import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CrudService } from 'src/app/services/crud.service';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { ToastrService } from 'ngx-toastr';
import { TicketService } from '../../services/ticket.service';
import { ActivatedRoute, Router } from '@angular/router';
import { UploadFileService } from 'src/app/services/upload-file.service';
import { NotificationsService } from 'src/services/notifications.service';
import { PermissionsService } from '../../services/permissions.service';
import { debounceTime, Subject } from 'rxjs';

@Component({
  selector: 'app-my-file-service',
  templateUrl: './my-file-service.component.html',
  styleUrls: ['./my-file-service.component.scss'],
})
export class MyFileServiceComponent {
  fileservices: any;
  pageNo: number = 1;
  pageSize: number = 10;
  totalRequest: number = 0;
  downloadFileList: any;
  showFilter: boolean = false;
  addi_info: boolean = false;
  ctrl_unit: boolean = false;
  country: boolean = false;
  engine_size: boolean = false;
  asso_custo: boolean = false;
  fuel_type: boolean = false;
  mileage: boolean = false;
  mileage_type: boolean = false;
  model: boolean = false;
  transmission: boolean = false;
  tuning_required: boolean = false;
  vehicle_VIN: boolean = false;
  vehicle_make: boolean = false;
  vehicle_registration: boolean = false;
  vehicle_year: boolean = false;
  fromDate: any;
  toDate: any;
  ticketCreateForm: FormGroup;
  formData: FormData = new FormData();
  ticketCategories: any;
  selectedFileReqId: any;
  type: any = this.ar.queryParams;
  permissions: any;
  showDownload: boolean = false;

  onSearch = new Subject();
  searchKeywords = '';
  sortField:any = '';
  sortOrder:any = '';

  isLoading = false;
  constructor(
    private crud: CrudService,
    private fb: FormBuilder,
    private router: Router,
    private toastr: ToastrService,
    private _ticketservice: TicketService,
    private _permissions: PermissionsService,
    private pageService: UploadFileService,
    private ar: ActivatedRoute,
    public noti: NotificationsService,
  ) {
    this.ticketCreateForm = this.fb.group({
      subject: ['', [Validators.required]],
      category: ['', [Validators.required]],
      file_request: ['', []],
      description: ['', [Validators.required]],
    });
    this.permissions = this._permissions.getPermissions();
  }

  
  ngOnInit() {
  
    var checkPage = this.pageService.getPageDetail();
    if (checkPage) this.pageNo = checkPage;

    if (this.type['_value'].chartType) {
      this.showDownload = true;
      var url;
      if (this.type['_value'].type == 'VehicleTuned') {
        if (this.type['_value'].chartType == 'month') {
          url = `dashboard/internal/technical/vehicle_tuned/type/data?filter_by=month&month=${this.type['_value'].month}&year=${this.type['_value'].year}&vehicle_type=${this.type['_value'].vehicle_type}`;
        } else {
          url = `dashboard/internal/technical/vehicle_tuned/type/data?filter_by=date&start_date=${this.type['_value'].start_date}&end_date=${this.type['_value'].end_date}&vehicle_type=${this.type['_value'].vehicle_type}`;
        }
        this.crud.getRequest(`${url}`).then((res: any) => {
          if (res) {
            this.fileservices = res.data;
          }
        });
      } else if (this.type['_value'].type == 'TechTeamDashToolUsage') {
        if (this.type['_value'].chartType == 'month') {
          url = `dashboard/internal/technical/tool/usage/data?month=${this.type['_value'].month}&year=${this.type['_value'].year}&filter_by=month`;
        } else {
          url = `dashboard/internal/technical/tool/usage/data?filter_by=date&start_date=${this.type['_value'].start_date}&end_date=${this.type['_value'].end_date}`;
        }
        this.crud.getRequest(`${url}`).then((res: any) => {
          if (res) {
            this.fileservices = res.data;
          }
        });
      } else if (this.type['_value'].type == 'FTMonthYear') {
        // {{url}}/dashboard/internal/technical/vehicle_tuned/category/data?start_date=2024-4-01&end_date=2024-4-10&filter_by=date&tuning_required=NO TUNE

        // {{url}}/dashboard/internal/technical/vehicle_tuned/category/data?filter_by=month&year=2024&month_number=1&tuning_required=NO TUNE

        if (this.type['_value'].chartType == 'month') {
          url = `dashboard/internal/technical/vehicle_tuned/category/data?filter_by=month&year=${this.type['_value'].year}&month_number=${this.type['_value'].month}&tuning_required=${this.type['_value'].tuneType}`;
        } else {
          url = `dashboard/internal/technical/vehicle_tuned/category/data?start_date=${this.type['_value'].start_date}&end_date=${this.type['_value'].start_date}&filter_by=date&tuning_required=${this.type['_value'].tuneType}`;
        }
        this.crud.getRequest(`${url}`).then((res: any) => {
          if (res) {
            this.fileservices = res.data;
            // this.totalRequest = res.count;
          }
        });
      }
    } else {
      if (this.type['_value'].id) this.searchFile(this.type['_value'].id);
      else {
        this.getRequestList();
      }
    }
  }
  savePageDetail() {
    this.pageService.savePageDetail(window.location.href, this.pageNo);
  }
  getTicketCategory() {
    this.crud
      .getRequest(`${requestUrl.getTicketCategoryList}`)
      .then((res: any) => {
        if (res) {
          this.ticketCategories = res.data.results;
          this.ticketCreateForm.patchValue({
            category: this.ticketCategories[0].id,
          });
        }
      });
  }
  downloadCsv(data: any[], fileName: string): void {
    const csvContent = this.constructCsvContent(data);
    const blob = new Blob([csvContent], { type: 'text/csv' });

    // Create link element to initiate download
    const a = document.createElement('a');
    a.href = window.URL.createObjectURL(blob);
    a.download = fileName;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  private constructCsvContent(data: any[]): string {
    let csvContent =
      'Request ID,Created By,Created At,Status,Credit,Vehicle Type,Ecu Brand,Ecu Version,Tool\n';

    // Loop through the data and construct CSV rows
    data.forEach((row) => {
      csvContent += `${row.request_id},${row.user?.first_name} ${
        row.user?.last_name
      } ,${this.noti.formatDate(row?.created_at)},${row.status},${
        row.credit_spent.credit || 'NA'
      } ${
        row.credit_spent?.credit
          ? row?.credit_spent?.credit_type == 1
            ? 'File Key'
            : row?.credit_spent?.credit_type == 2
            ? 'EVC'
            : 'Function Key'
          : ''
      },${row.vehicle_type},${row.ecu_brand},${row.ecu_version},${
        row.tuning_tool_used
      }\n`;
    });

    return csvContent;
  }
  getRequestList(field?:string, order?:string) {
    if(field) {
      this.sortField = field;
      this.sortOrder = order;
    }
    
    let filter = '';

    if(this.sortField) {
      filter += `&field=${this.sortField}&order_in=${this.sortOrder}` || '';
    }
    this.isLoading = true;
    this.crud
      .getRequest(
        `${requestUrl.getRequestListBySearch}${this.searchKeywords ? `&search=${this.searchKeywords}` : ''}&page=${this.pageNo}&page_size=${this.pageSize}${filter}`
      )
      .then((res: any) => {
        this.isLoading = false;
        this.fileservices = res.data.results;
        this.totalRequest = res.data.count;
      })
      .finally(() => this.isLoading = false);
  }
  getRequestFileOnFilter(field: any, type: any) {
    this.crud
      .getRequest(
        `${requestUrl.getRequestList}?field=${field}&order_in=${type}`
      )
      .then((res: any) => {
        this.fileservices = res.results;
        this.totalRequest = res.count;
      });
  }
  searchFile(event: any) {
    this.pageNo = 1;
    if (event.trim().length > 0) {
      this.searchKeywords = event;
      this.crud
      .getRequest(`${requestUrl.getRequestListBySearch}${event}`)
      .then((res: any) => {
          this.fileservices = res.data.results;
          this.totalRequest = res.data.count;
        });
    } else {
      this.searchKeywords = "";
      this.getRequestList();
    }
  }
  serchFileAgainstDate() {
    if (this.fromDate && this.toDate) {
      this.crud
        .getRequest(
          `${requestUrl.getRequestListByDateSearch}?from_date=${this.fromDate}&to_date=${this.toDate}`
        )
        .then((res: any) => {
          this.fileservices = res.data;
          this.totalRequest = res.count;
          document.getElementById('dateModal')?.click();
        });
    }
  }
  handlePageChange(event: any): void {
    this.pageNo = event;
    this.getRequestList();
  }

  hasAdditionalFunctionColumn = false;
  getFileList(id: any) {
    this.hasAdditionalFunctionColumn = false;
    this.crud
      .getRequest(`${requestUrl.fileDownloadList}${id}`)
      .then((res: any) => {
        if (res) {
          this.downloadFileList = res.data;
          this.downloadFileList.forEach((element: any, index: any) => {
            this.downloadFileList[index].newTitle = element.title.split('/');
          });
          this.downloadFileList = this.downloadFileList.map((el:any) => {
            if('additional_function_list' in el && el.additional_function_list.length) {
              this.hasAdditionalFunctionColumn = true
  
              el.functionList = el.additional_function_list.map((el:any) => {
                const key = Object.keys(el)[0]
                return {
                  key,
                  value:el[key]
                }
              })
            }
            return el;
          })
          console.log(this.downloadFileList, 'download file list');
        }
      });
  }
  tdControl(type: string) {
    if (type == 'addi_info') this.addi_info = !this.addi_info;
    else if (type == 'ctrl_unit') this.ctrl_unit = !this.ctrl_unit;
    else if (type == 'country') this.country = !this.country;
    else if (type == 'engine_size') this.engine_size = !this.engine_size;
    else if (type == 'asso_custo') this.asso_custo = !this.asso_custo;
    else if (type == 'fuel_type') this.fuel_type = !this.fuel_type;
    else if (type == 'mileage') this.mileage = !this.mileage;
    else if (type == 'mileage_type') this.mileage_type = !this.mileage_type;
    else if (type == 'model') this.model = !this.model;
    else if (type == 'transmission') this.transmission = !this.transmission;
    else if (type == 'tuning_required')
      this.tuning_required = !this.tuning_required;
    else if (type == 'vehicle_VIN') this.vehicle_VIN = !this.vehicle_VIN;
    else if (type == 'vehicle_make') this.vehicle_make = !this.vehicle_make;
    else if (type == 'vehicle_registration')
      this.vehicle_registration = !this.vehicle_registration;
    else if (type == 'vehicle_year') this.vehicle_year = !this.vehicle_year;
  }
  ticketCreateFormSubmit() {
    if (this.ticketCreateForm.valid) {
      this.formData.append('subject', this.ticketCreateForm.value.subject);
      this.formData.append('category', this.ticketCreateForm.value.category);
      this.formData.append(
        'description',
        this.ticketCreateForm.value.description
      );
      this.formData.append('file_request', this.selectedFileReqId);
      this._ticketservice.createTicket(this.formData).subscribe(
        (res: any) => {
          this.toastr.success(res.message, '', { timeOut: 4000 });
          document.getElementById('hidechangeemailmodal')?.click();
          this.formData.delete('subject');
          this.formData.delete('category');
          this.formData.delete('description');
          this.formData.delete('file_request');
          this.ticketCreateForm.reset();
          this.ticketCreateForm.patchValue({
            category: this.ticketCategories[0].id,
          });
        },
        (err: any) => {
          this.toastr.error(err.error.detail, '', { timeOut: 4000 });
        }
      );
    } else {
      this.markAllControlsAsTouched(this.ticketCreateForm);
    }
  }
  markAllControlsAsTouched(formGroup: FormGroup) {
    Object.values(formGroup.controls).forEach((control) => {
      control.markAsTouched();

      if (control instanceof FormGroup) {
        this.markAllControlsAsTouched(control);
      }
    });
  }
  resetForm() {
    this.ticketCreateForm.markAsUntouched();
    this.ticketCreateForm.reset();
    this.ticketCreateForm.patchValue({ category: this.ticketCategories[0].id });
  }
  getDirectoryList(id: any) {
    this.crud
      .getRequest(`${requestUrl.getRequestDirectory}${id}`)
      .then((res: any) => {
        if (res && res.data.is_file_dir_exist) {
          this.router.navigateByUrl(
            '/admin/directory/view-directory/' + res.data.diretory_name
          );
        } else {
          this.toastr.error('No files exist.');
        }
      });
  }

  onEmptyUrl(): void {
    this.toastr.error('File not found!');
  }

  checkExistingTicket(toggle:HTMLAnchorElement,id:string) {
    this._ticketservice.getTicketForFileRequest(id).subscribe({
      next:(res:any) => {
        if(("message" in res) && res.message === 'Ticket found.') {
          this.router.navigateByUrl(`admin/tickets/view-ticket/TK${res.ticket_id}/${res.ticket_id}`)
        } else {
          toggle.click();
        }
      },
      error: _ => toggle.click(),
    })
  }
}
