import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';

@Component({
  selector: 'app-view-file-service',
  templateUrl: './view-file-service.component.html',
  styleUrls: ['./view-file-service.component.scss']
})
export class ViewFileServiceComponent {
  requestId: any;
  requestDetail: any;

  constructor(private crud: CrudService , private route: ActivatedRoute){
    this.requestId = this.route.snapshot.paramMap.get('id');

  }
  ngOnInit(){
    this.crud.getRequest(`${requestUrl.getRequestDetail}${this.requestId}`).then((res:any)=>{
      this.requestDetail = res;
      if("other_fields" in this.requestDetail && this.requestDetail.other_fields) {
        this.requestDetail.other_fields = this.requestDetail.other_fields.replaceAll('_',' ').replaceAll(',',', ');
      }
    })
  }

}
