import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { CrudService } from 'src/app/services/crud.service';
import { NotificationsService } from 'src/services/notifications.service';
@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.scss']
})
export class TimelineComponent {

  timelinedata: any = [];
  path: string = '/admin/fileservice'
  type: any = this.ar.queryParams;
  isLoading = false;
  constructor(private crud: CrudService, private ar: ActivatedRoute,
    public notification: NotificationsService) {
    if (this.type['_value'].fromTicket)
      this.path = '/admin/tickets';
  }
  ngOnInit() {
    let requestId = this.ar.snapshot.paramMap.get('id');
    this.isLoading = true;
    this.crud.getRequest(`${requestUrl.getFileHistory}${requestId}`).then((res: any) => {
      if (res) {
        this.timelinedata = res;
      }
    }).finally(() => {
      this.isLoading = false
    })
  }


}
