import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ViewFileServiceComponent } from './view-file-service/view-file-service.component';
import { MyFileServiceComponent } from './my-file-service/my-file-service.component';
import { FileserviceComponent } from './fileservice.component';
import { TimelineComponent } from './timeline/timeline.component';

const routes: Routes = [{
  path: '', component:FileserviceComponent, children: [
    { path: '', component: MyFileServiceComponent },
    { path: 'view/:id', component: ViewFileServiceComponent },
    { path: 'timeline/:id', component:TimelineComponent }

  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FileserviceRoutingModule { }
